@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

.btn {
    background: linear-gradient(
      92.23deg,
      #ff56f6 21.43%,
      #b936ee 50.63%,
      #3bace2 100%,
      #406aff 117.04%
    );
    box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51);
  }
  
  .btn:hover {
    background: linear-gradient(
      92.23deg,
      #406aff 21.43%,
      #3bace2 50.63%,
      #b936ee 100%,
      #ff56f6 117.04%
    );
  }
  
  .gradient,
  .dark .active {
   background:linear-gradient(
      92.23deg,
      #406aff 21.43%,
      #3bace2 50.63%,
      #b936ee 100%,
      #ff56f6 117.04%
    );}
    .active {
   background:linear-gradient(
      92.23deg,
      #1894cb 50.63%,
      #2f495c 100%,
      #ccd3d9 117.04%
    );
  }


  
  .active {
    color: #fff;
    padding: 1rem;
    border-radius: 100%;
  }
  
  
 

  